import React from 'react';
import { connect } from 'react-redux';
import {
	loadProfile,
	getStatus,
	updateStatus,
	updateAvatar,
	updateProfile,
} from '../../redux/profileReducer';
import Profile from './Profile';
import { withRouter } from 'react-router-dom';
import { withAuthRedirect } from '../hoc/withAuthRedirect';
import { compose } from 'redux';
import Preloader from '../Preloader/Preloader';

class ProfileContainer extends React.Component {
	updateProfile = () => {
		const userId = this.props.match.params.userId ?? this.props.authorizedUserId;
		this.props.loadProfile(userId);
		this.props.getStatus(userId);
	};

	componentDidMount() {
		this.updateProfile();
	}

	componentDidUpdate(prevProps, prevState) {
		if (prevProps.match.params.userId !== this.props.match.params.userId) {
			this.updateProfile();
		}
	}

	render() {
		if (this.props.isFetching) return <Preloader />;
		return (
			<Profile
				profile={this.props.profile}
				status={this.props.status}
				updateStatus={this.props.updateStatus}
				updateAvatar={this.props.updateAvatar}
				updateProfile={this.props.updateProfile}
				isMyProfile={this.props.authorizedUserId === this.props?.profile?.userId}
			/>
		);
	}
}

const mapStateToProps = (state) => ({
	profile: state.profilePage.profile,
	status: state.profilePage.status,
	authorizedUserId: state.auth.id,
	isFetching: state.profilePage.isFetching,
});

export default compose(
	connect(mapStateToProps, { loadProfile, getStatus, updateStatus, updateAvatar, updateProfile }),
	withRouter,
	withAuthRedirect
)(ProfileContainer);
